/* ===========================
   Landing: two-column hero (edge-to-edge)
   =========================== */

/* tweakable knobs */
:root {
  --split-left: 36%;        /* width of left stripe (try 35–40%) */
  --profile-size: 420px;    /* circle diameter (try 340–460px) */
}

/* section wrapper */
.landing {
  min-height: 100vh;
  width: 100vw;
  overflow-x: hidden;
}

/* full-width grid split (NO max-width cap) */
.landing--container {
  position: relative;
  width: 100%;
  max-width: none;                         /* <- stay edge-to-edge */
  height: 100vh;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 36% calc(100% - 36%);
  grid-template-columns: var(--split-left) calc(100% - var(--split-left));
  box-sizing: border-box;
}

/* ========== Left stripe ========== */
.landing--left {
  grid-column: 1 / 2;
  min-height: 100vh;
  position: relative;                      /* allow absolute children */
  display: block;
  box-sizing: border-box;
}

/* socials row at the bottom of the left stripe (desktop) */
.lcl--content {
  position: absolute;
  bottom: 2.5rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1.6rem;
  gap: 1.6rem;
}

/* social icons */
.landing--social {
  font-size: 50px;
  transition: transform 0.18s ease;
}
.landing--social:hover { transform: scale(1.12); }

/* ========== Right column ========== */
.landing--right {
  grid-column: 2 / 3;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 0;                         /* push inner content instead */
  box-sizing: border-box;
}

/* content block (name + buttons) */
.lcr--content {
  position: relative;
  z-index: 30;
  width: 70%;
  max-width: 680px;
  margin-left: calc(420px * 0.9);
  margin-left: calc(var(--profile-size) * 0.9);   /* distance from the circle */
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji";
  font-family: var(--primaryFont, ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji");
}

.lcr--content h6 {
  margin: 0;
  font-weight: 500;
  font-size: 1.4rem;
  opacity: 0.85;
}

.lcr--content h1 {
  margin: 0;
  font-weight: 800;
  font-size: 3.4rem;
  line-height: 1.02;
}

.lcr-buttonContainer {
  margin-top: 0.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  flex-wrap: wrap;
}

/* a mobile-only socials row under the buttons */
.socials--mobile { display: none; }

/* ========== Big circular photo ========== */
.landing--profile {
  position: absolute;
  top: 50%;
  left: 36%;
  left: var(--split-left);
  transform: translate(-50%, -50%);
  width: 420px;
  width: var(--profile-size);
  height: 420px;
  height: var(--profile-size);
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 20px 50px rgba(0,0,0,.35);
  z-index: 20;
  transition: transform 180ms ease, opacity 180ms ease;
}

/* ---------- Responsive ---------- */
@media (max-width: 1150px) {
  :root { --split-left: 33%; --profile-size: 360px; }
  .lcr--content { margin-left: calc(420px * 0.60); margin-left: calc(var(--profile-size) * 0.60); }
  .lcr--content h1 { font-size: 2.8rem; }
}

@media (max-width: 900px) {
  :root { --split-left: 40%; --profile-size: 300px; }
  .lcr--content { margin-left: calc(420px * 0.50); margin-left: calc(var(--profile-size) * 0.50); }
}

@media (max-width: 700px) {
  .landing--container {
    grid-template-columns: 1fr;
    height: auto;
    padding: 2rem 1rem;
  }

  /* hide left stripe on mobile, so we add a mobile socials row */
  .landing--left { display: none; }

  .landing--profile {
    position: relative;
    top: 0; left: 50%; transform: translate(-50%, 0);
    width: 200px; height: 200px; margin: 0 auto 1.25rem;
    box-shadow: 0 10px 30px rgba(0,0,0,.22);
  }

  .landing--right { grid-column: 1; justify-content: center; }

  .lcr--content {
    width: 100%;
    max-width: 720px;
    text-align: center;
    align-items: center;
    margin-left: 0;                      /* reset push */
  }

  .lcr--content h1 { font-size: 2rem; }
  .socials--mobile { display: flex; grid-gap: 1.25rem; gap: 1.25rem; margin-top: 1rem; }
  .landing--social { font-size: 28px; }
}

@media (max-width: 420px) {
  .landing--profile { width: 160px; height: 160px; }
  .lcr--content h1 { font-size: 1.6rem; }
}


