.singleProject {
    box-shadow: 4px 4px 8px rgba(36, 3, 3, 0.2);
    width: 304px;
    height: 360px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1.4rem 2rem;
    position: relative;
    cursor: pointer;
    overflow: hidden;
}

.projectContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
}

.projectContent > h2 {
    font-family: var(--primaryFont);
    font-style: normal;
    font-weight: 600;
    font-size: 1.525rem;
    line-height: 110%;
    text-align: center;
}

.projectContent > img {
    width: 100%;
    height: 60%;
    transition: opacity 0.7s 0.3s;
}

.singleProject:hover img{
    opacity: 0;
}

.project--showcaseBtn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.project--showcaseBtn > a {
    cursor: pointer;
    text-decoration: none;
    transition: transform 0.5s 0.3s;
}

.singleProject:hover .project--showcaseBtn a:nth-child(2) {
    transform: translateX(-140px) scale(1.1) !important;
}


.singleProject .project--desc{
    position: absolute;
    width: 95%;
    height: 160px;
    left: 0;
    top: 0;
    transform: translate(-110%,40%);
    padding: 15px;
    border-radius: 0 20px 20px 0;
    transition: transform 0.9s;
    line-height: 110%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: var(--primaryFont);
}
  
.singleProject:hover .project--desc{
    transform: translate(-2%,40%);
}
  
.singleProject .project--lang{
    position: absolute;
    bottom: 20px;
    right: 0;
    width: 140px;
    font-size: 0.8rem;
    display: flex;
    justify-content: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    flex-direction: column;
    border-radius: 10px 0 0 10px;
    transform: translateX(100%);
    transition: transform 0.5s 0.3s;
    padding: 0.825rem;
}
  
.singleProject:hover .project--lang{
    transform: translateX(0);
}

.project--lang > span {
    font-family: var(--primaryFont);
    font-weight: 500;
    word-break: break-word; 
    line-height: 100%;
}
/* Volunteers Section */
.volunteers {
    min-height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 0.1rem;    /* less space above */
    padding-bottom: 6rem;  /* more space below */
    padding-left: 3rem;
    padding-right: 3rem;
}

/* Volunteer Section Title and Body */
.volunteer-body {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 800px; /* max width for better readability */
}

.volunteer-body h1 {
    font-family: var(--primaryFont);
    font-size: 3rem;
    margin-bottom: 2rem;
    text-align: center;
}

.volunteer-body h4 {
    font-size: 1.75rem;
    font-family: 'Caveat', cursive;
    font-weight: 400;
    text-align: center;
}

/* Cards Container */
.volunteer-cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

/* Individual Volunteer Card */
.volunteercard {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem;
    border-radius: 20px;
    margin: 1.5rem 0;
    max-width: 700px; /* card width limit */
    width: 90%;        /* responsive width */
    background-color: var(--primary30);
    transition: background-color 200ms ease-in-out;
}

.volunteercard:hover {
    background-color: var(--primary50);
}

/* Content inside the card */
.volunteercard-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    flex: 1 1;
}

.volunteercard-details1 h2 {
    font-family: var(--primaryFont);
    font-weight: 600;
    font-size: 1.8rem;
    margin-bottom: 0.5rem;
}

.volunteercard-details1 p {
    font-size: 1.1rem;
    font-family: Roboto;
    font-weight: 400;
    color: var(--tertiary80);
}

/* Date and field above the title */
.volunteercard-details2 {
    display: flex;
    flex-direction: column;
    grid-gap: 0.3rem;
    gap: 0.3rem;
    margin-bottom: 0.5rem;
}

.volunteercard-details2 h5 {
    font-size: 1.2rem;
    font-weight: 500;
    color: var(--primary);
}

/* Image container on the right */
.volunteercard-imgcontainer {
    width: 150px;
    display: flex;
    justify-content: center;
    margin-left: 1rem;
}

.volunteercard-imgcontainer > img {
    width: 150px;
    height: 150px;
    border-radius: 10px;
    object-fit: contain;
}

/* Media Queries */

/* Medium screens */
@media screen and (max-width: 992px) {
    .volunteercard {
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    .volunteercard-imgcontainer {
        margin-left: 0;
        margin-top: 1rem;
    }

    .volunteercard-imgcontainer > img {
        width: 120px;
        height: 120px;
    }
}

/* Small screens */
@media screen and (max-width: 600px) {
    .volunteer-body h1 {
        font-size: 2.2rem;
    }

    .volunteercard-details1 h2 {
        font-size: 1.5rem;
    }

    .volunteercard-details1 p {
        font-size: 1rem;
    }

    .volunteercard-details2 h5 {
        font-size: 1.1rem;
    }

    .volunteercard-imgcontainer > img {
        width: 100px;
        height: 100px;
    }
}


